___CSS_LOADER_EXPORT___.push([module.id, ".body{font-family:Avenir,Arial Rounded MT Bold,Comic Sans MS,Hannotate SC,Hiragino Kaku Gothic ProN,ヒラギノ角ゴ ProN W3,Yu Gothic UI,sans-serif}input,textarea{width:100%}textarea{height:200px}.error{color:#666}#overlay{align-items:center;background-color:rgba(0,0,0,.5);display:flex;height:100%;justify-content:center;left:0;position:fixed;top:0;width:100%;z-index:1}#book{background:#fff6e6;height:100vh;overflow:auto;overflow:scroll;padding:1em;width:50%;z-index:2}.create{font-size:2rem;font-weight:700}.input{background:#fff6e6;box-shadow:0 2px 3px rgba(0,0,0,.22);margin:3rem auto 0;padding:2.2rem .8rem;width:70%}.input:after{background:#db7093;content:\"\";height:35px;left:10px;margin:-35px auto 10px 35%;opacity:.3;position:absolute;right:10px;top:185px;transform:rotate(-2deg);width:30%}.input-content{margin:auto;width:50%}.input-content_header{display:flex;justify-content:space-between}.input-content_header-title{border-bottom:2px solid #fff;font-size:2rem;font-weight:700}.input-content_body{margin-top:1rem}.search-btn{background-color:#eb6100;border-radius:10px;color:#fff;padding:1rem;width:40%}.search-btn:hover{background:#f56500}.input-btn{background-color:#ffc7af;border-radius:10px;padding:.5rem 1rem}:disabled{cursor:not-allowed;opacity:.3}.close{height:30px;width:30px}.select-btn{background-color:#eb6100;border-radius:10px;color:#fff;margin-top:2rem;padding:.5rem}.select-btn:hover{background:#f56500}.book-search{display:flex;justify-content:space-between}.book-search_main{display:flex;margin-top:2rem;width:80%}.book-search_main-input{border-radius:30px;margin:auto;width:80%}.book-search_main-btn{background-color:#ffc7af;border-radius:10px;padding:.5rem 1rem}.book-body{display:flex;flex-wrap:wrap}.book-body div{width:44.2%}.book-body_content{display:flex;margin:2rem 1rem}.book-body_content-img img{height:200px;width:80%}@media screen and (max-width:1024px){.input{margin-top:8rem;padding:1rem .5rem;width:90%}.input:after{background:#db7093;content:\"\";height:35px;margin:-35px auto 10px;opacity:.3;position:absolute;top:20px;transform:rotate(-2deg);width:50%}.input-content_header-title{border:none;width:80%}.input-content{width:100%}.input-content_header{font-size:1rem;margin-top:1rem}#book{margin-top:7rem}#book,.book-search_main{width:100%}.book-search_main-btn{margin-left:.3rem}.book-body{display:block}.book-body div{margin:1rem auto;width:80%}}", "",{"version":3,"sources":["webpack://./resources/js/components/BookCreateComponent.vue"],"names":[],"mappings":"AAyJA,MACA,oIACA,CAEA,eACA,UACA,CAEA,SACA,YACA,CAEA,OACA,UACA,CAGA,SASA,kBAAA,CAFA,+BAAA,CACA,YAAA,CAFA,WAAA,CAIA,sBAAA,CANA,MAAA,CAFA,cAAA,CACA,KAAA,CAEA,UAAA,CAJA,SAWA,CAEA,MAKA,kBAAA,CAFA,YAAA,CAGA,aAAA,CACA,eAAA,CAHA,WAAA,CAFA,SAAA,CADA,SAOA,CAIA,QAEA,cAAA,CADA,eAEA,CAEA,OAGA,kBAAA,CACA,oCAAA,CAHA,kBAAA,CAIA,oBAAA,CAHA,SAIA,CAEA,aAQA,kBAAA,CANA,UAAA,CAGA,WAAA,CAKA,SAAA,CAHA,0BAAA,CADA,UAAA,CALA,iBAAA,CAUA,UAAA,CARA,SAAA,CAMA,uBAAA,CALA,SAQA,CAEA,eAEA,WAAA,CADA,SAEA,CAEA,sBACA,YAAA,CACA,6BACA,CAEA,4BAGA,4BAAA,CAFA,cAAA,CACA,eAEA,CAEA,oBACA,eACA,CAGA,YAIA,wBAAA,CACA,kBAAA,CAJA,UAAA,CAEA,YAAA,CADA,SAIA,CAEA,kBACA,kBACA,CAGA,WAEA,wBAAA,CACA,kBAAA,CAFA,kBAGA,CAEA,UAEA,kBAAA,CADA,UAEA,CAEA,OAEA,WAAA,CADA,UAEA,CAGA,YAIA,wBAAA,CACA,kBAAA,CAJA,UAAA,CACA,eAAA,CACA,aAGA,CAEA,kBACA,kBACA,CAGA,aACA,YAAA,CACA,6BACA,CAEA,kBACA,YAAA,CACA,eAAA,CACA,SACA,CAEA,wBAGA,kBAAA,CAFA,WAAA,CACA,SAEA,CAEA,sBAEA,wBAAA,CACA,kBAAA,CAFA,kBAGA,CAEA,WACA,YAAA,CACA,cACA,CAEA,eACA,WACA,CAEA,mBACA,YAAA,CACA,gBACA,CAEA,2BAEA,YAAA,CADA,SAEA,CAGA,qCAKA,OAGA,eAAA,CADA,kBAAA,CADA,SAGA,CAEA,aAQA,kBAAA,CANA,UAAA,CAGA,WAAA,CAEA,sBAAA,CADA,UAAA,CALA,iBAAA,CAEA,QAAA,CAMA,uBAAA,CALA,SAMA,CAEA,4BACA,WAAA,CACA,SACA,CAEA,eACA,UACA,CAEA,sBAEA,cAAA,CADA,eAEA,CAGA,MAEA,eACA,CAEA,wBAJA,UAMA,CAEA,sBACA,iBACA,CAEA,WACA,aACA,CAEA,eAEA,gBAAA,CADA,SAEA,CACA","sourcesContent":["<template>\n    <div class=\"body\">\n        <div class=\"input\">\n            <div class=\"input-content\">\n                <div v-for=\"error in errors\" :key=\"error.id\">\n                    <div class=\"error\"><p>{{error}}</p></div>\n                </div>\n                <div class=\"input-content_header\">\n                    <h2 class=\"input-content_header-title\">新規作成</h2>\n                    <button class=\"search-btn\" @click=\"openModal\">本を検索する</button> \n                </div>\n                <validation-observer tag=\"form\" ref=\"obs\" v-slot=\"ObserverProps\" v-on:submit.prevent=\"submit\">  \n                    <div class=\"input-content_body\">\n                        <validation-provider v-slot=\"ProviderProps\" rules=\"required|max:50\">\n                            <p>タイトル</p>\n                            <input type=\"text\" v-model=\"item.title\" name=\"タイトル\">\n                            <div class=\"error\">{{ProviderProps.errors[0]}}</div>\n                        </validation-provider>\n                    </div>\n                    <div class=\"input-content_body\">\n                        <validation-provider v-slot=\"ProviderProps\" rules=\"required|max:30\">\n                            <p>著者</p>\n                            <input type=\"text\"  v-model=\"item.author\" name=\"著者\">\n                            <div class=\"error\">{{ProviderProps.errors[0]}}</div>\n                        </validation-provider>\n                    </div>\n                    <div class=\"input-content_body\">\n                        <p class=\"input_rating-title\">評価</p>\n                        <star-rating v-model=\"rating\" v-bind:increment=\"0.5\"></star-rating>\n                    </div>\n                    <div class=\"input-content_body\">\n                        <validation-provider v-slot=\"ProviderProps\" rules=\"required|max:500\">\n                            <p>感想</p>\n                            <textarea v-model=\"item.report\" name=\"感想\"></textarea>\n                            <div class=\"error\">{{ProviderProps.errors[0]}}</div>\n                        </validation-provider>\n                    </div>\n                    <button class=\"input-btn\" type=\"submit\" :disabled=\"ObserverProps.invalid\">登録</button>\n                </validation-observer>\n            </div>\n        </div>\n        \n        <div id=\"overlay\" v-show=\"showContent\" @click.self=\"closeModal\">\n            <div id=\"book\">\n                <div class=\"book-search\">\n                    <div class=\"book-search_main\">\n                        <input type=\"text\" class=\"book-search_main-input\" placeholder=\"キーワード入力してください。\" v-model=\"query\">\n                        <button class=\"book-search_main-btn\" @click=\"getBook(query)\">検索</button>\n                    </div>\n                    <img class=\"close\" @click=\"closeModal\" src=\"/images/close.png\">\n                </div>\n                <p v-if=\"bookEmpty\">検索結果がありません</p>\n                <div class=\"book-body\">\n                    <div class=\"book-body_content\" v-for=\"searchData in searchDatas\" :key=\"searchData.id\">\n                        <div class=\"book-body_content-img\">\n                            <img v-bind:src=\"searchData.volumeInfo.imageLinks&&searchData.volumeInfo.imageLinks.smallThumbnail\" >\n                        </div>\n                        <div class=\"book-body_content-main\">\n                            <p>{{searchData.volumeInfo.title}}</p>\n                            <p>{{searchData.volumeInfo.authors}}</p>\n                            <button class=\"select-btn\" @click=\"dataSet(searchData)\">選択する</button>\n                        </div>  \n                    </div>\n                </div>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script>\n    export default {\n        props:['user'],\n        data(){\n            return {\n                rating:0,\n                item:{\n                    user_id:this.user.id,\n                    title:'',\n                    author:'',\n                    imge:'/images/noimage.jpg',\n                },  \n                searchDatas:'',\n                showContent:false,\n                query:'',\n                bookEmpty:false,\n                errors:{},\n        \n            }\n        },\n        methods: {\n        // データーベース登録処理\n            submit(){\n                const baseUrl = process.env.MIX_API_URL;\n                this.item.rating = this.rating;\n                axios.post(baseUrl,this.item)\n                    .then((res)=>{\n                        this.$swal('登録が完了しました!',{\n                            icon: \"success\",\n                        });\n                        console.log(this.item);\n                        this.$router.push({name: 'book.home'});\n                    })\n                    .catch((error) =>{\n                        this.errors = error.response.data.errors;\n                        console.log(this.errors);\n                        console.log(error);\n                        this.$swal('登録に失敗しました。もう一度お試しください',{\n                            icon: \"error\",\n                        })\n                    })\n            },\n\n        // 本情報検索処理\n            getBook(query){\n                axios.get(`https://www.googleapis.com/books/v1/volumes?q=serch+${query}&maxResults=15`)\n                .then((res) => {\n                    if(res.data.items != null){\n                        this.searchDatas = res.data.items;\n                        this.bookEmpty = false;\n                    }else{\n                        this.searchDatas = '';\n                        this.bookEmpty = true;\n                    }\n                    \n                    console.log(this.searchDatas);\n                })\n                .catch((error) => {\n                    console.log(error);\n                    alert('失敗です')\n                })\n            },\n            openModal(){\n                this.showContent = true;\n                console.log('動いている');\n            },\n            closeModal(){\n                this.showContent = false;\n            },\n\n        // 本検索結果form反映処理\n            dataSet(data){\n                this.item.title = data.volumeInfo.title;\n                this.item.author = data.volumeInfo.authors[0];\n                this.item.imge = data.volumeInfo.imageLinks.smallThumbnail;\n                return this.closeModal();\n                \n            }\n        },\n    }\n</script>\n\n<style>\n\n    .body {\n        font-family: 'Avenir','Arial Rounded MT Bold','Comic Sans MS','Hannotate SC','Hiragino Kaku Gothic ProN','ヒラギノ角ゴ ProN W3','Yu Gothic UI',sans-serif;\n    }   \n\n    input,textarea {\n        width: 100%;\n    }\n\n    textarea {\n        height: 200px;\n    }\n\n    .error {\n        color: #666666;\n    }\n    \n/* モーダルcss     */\n    #overlay{\n        z-index:1;\n        position:fixed;\n        top:0;\n        left:0;\n        width:100%;\n        height:100%;\n        background-color:rgba(0,0,0,0.5);\n        display:flex;\n        align-items:center;\n        justify-content:center;\n        \n    }\n\n    #book{\n        z-index:2;\n        width:50%;\n        height:100vh;\n        padding:1em;\n        background:#FFF6e6;\n        overflow: auto;\n        overflow: scroll;\n    }\n\n\n/* フォーム */\n    .create {\n        font-weight: bold;\n        font-size: 2rem;\n    }\n\n    .input {\n        margin: 3rem auto 0;\n        width: 70%;\n        background :#FFF6e6;\n        box-shadow: 0 2px 3px rgba(0,0,0,.22);\n        padding: 2.2rem 0.8rem 2.2rem 0.8rem;\n    }\n\n    .input:after {\n        position: absolute;\n        content: '';\n        top: 185px;\n        width: 30%;\n        height: 35px;\n        opacity: 0.3;\n        margin: -35px auto 10px 35%;\n        background: #db7093;\n        transform: rotate(-2deg);\n        left: 10px;\n        right: 10px;\n    }\n\n    .input-content {\n        width: 50%;\n        margin: auto;\n    }\n\n    .input-content_header {\n        display: flex;\n        justify-content: space-between;\n    }\n    \n    .input-content_header-title {\n        font-size: 2rem;\n        font-weight: bold;\n        border-bottom: 2px solid #fff;\n    }\n\n    .input-content_body {\n        margin-top : 1rem;\n    }\n\n/* 本検索ボタン */\n    .search-btn {\n        color: #fff;\n        width: 40%;\n        padding: 1rem;\n        background-color: #eb6100;\n        border-radius: 10px;\n    }\n\n    .search-btn:hover {\n        background: #f56500;\n    }\n\n/* 送信ボタン */\n    .input-btn{\n        padding: 0.5rem 1rem;\n        background-color: #FFC7AF;\n        border-radius: 10px;\n    }\n\n    :disabled {\n        opacity: 0.3;\n        cursor: not-allowed;\n    }\n\n    .close {\n        width: 30px;\n        height: 30px; \n    }\n\n/* 本選択ボタン */\n    .select-btn {\n        color: #fff;\n        margin-top: 2rem;\n        padding: 0.5rem;\n        background-color: #eb6100;\n        border-radius: 10px;\n    }\n\n    .select-btn:hover {\n        background: #f56500;\n    }\n\n/* 本検索結果 */\n    .book-search {\n        display: flex;\n        justify-content: space-between;\n    }\n\n    .book-search_main {\n        display: flex;\n        margin-top: 2rem;\n        width: 80%;\n    }\n\n    .book-search_main-input {\n        margin: auto;\n        width: 80%;\n        border-radius: 30px;\n    }\n\n    .book-search_main-btn {\n        padding: 0.5rem 1rem;\n        background-color: #FFC7AF;\n        border-radius: 10px;\n    }\n\n    .book-body {\n        display: flex;\n        flex-wrap: wrap;\n    }\n\n    .book-body div {\n        width: 44.2%;\n    }\n\n    .book-body_content {\n        display: flex;\n        margin: 2rem 1rem;\n    }\n\n    .book-body_content-img img {\n        width: 80%;\n        height: 200px;\n    }\n\n\n    @media screen and (max-width:1024px) {\n        /* body {\n            margin-top: 5rem;\n        } */\n        \n        .input {\n            width:90%;\n            padding: 1rem 0.5rem;\n            margin-top: 8rem;\n        }\n\n        .input:after {\n            position: absolute;\n            content: '';\n            top: 20px;\n            width: 50%;\n            height: 35px;\n            opacity: 0.3;\n            margin: -35px auto 10px;\n            background: #db7093;\n            transform: rotate(-2deg);\n        }\n\n        .input-content_header-title {\n            border: none;\n            width: 80%;\n        }\n\n        .input-content {\n            width: 100%;\n        }\n\n        .input-content_header {\n            margin-top: 1rem;\n            font-size: 1rem;\n        }\n\n        /* モーダル */\n        #book {\n            width:100%;\n            margin-top: 7rem;\n        }\n\n        .book-search_main {\n            width: 100%;\n        }\n\n        .book-search_main-btn {\n            margin-left: 0.3rem;\n        }\n\n        .book-body {\n            display: block;\n        }\n\n        .book-body div {\n            width: 80%;\n            margin: 1rem auto;\n        }\n    }\n\n    \n</style>"],"sourceRoot":""}]);
// Exports
export default ___CSS_LOADER_EXPORT___;
