         <p>みんなの投稿一覧</p>\n                        <img src=\"/images/otherlist.jpg\" alt=\"投稿一覧アイコン\">\n                    </router-link>\n                </li>\n            </ul>\n        </div>\n    </div>\n</template>\n\n<script>\n\nexport default ({\n    data(){\n        return {\n            active: false,\n            resp: false,\n        }\n    },\n    methods: {\n\n    //画面検知    \n        handleResize(){\n            if(window.innerWidth <= 1024){\n                this.resp = true;\n            }else {\n                this.resp = false;\n            }\n        },\n    },\n    created(){\n        window.addEventListener('resize',this.handleResize);\n        this.handleResize();\n    }\n})\n</script>\n\n<style scoped>\n\n.body {\n    width: 100%;\n    font-family: 'Avenir','Arial Rounded MT Bold','Comic Sans MS','Hannotate SC','Hiragino Kaku Gothic ProN','ヒラギノ角ゴ ProN W3','Yu Gothic UI',sans-serif;\n    background-color: #FFDBC9;\n}\n\n/* スマホタイトル css  */\n.header-resp {\n    position: fixed; \n    top: 0;\n    background: #FFDBC9;\n    width: 100%;\n    height: 70px;\n    z-index: 50;\n\n}\n\n.header-title_resp {\n    position: fixed; \n    top: 0;\n    left: 25%;\n    align-items: center;\n    font-size: 3rem;\n    color: #FF5F17;\n    font-weight:bold;\n    z-index: 50;\n}\n\n\n/* ハンバーガーメニューcss */\n.header-btn {\n    position: fixed;\n    top: 10px;\n    left: 0;\n    width: 70px;\n    height: 70px;\n    cursor: pointer;\n    z-index: 60;\n}\n\n\n.line {\n    position: absolute;\n    top: 0;\n    left: 20px;\n    width: 45px;\n    height: 2px;\n    background: #000;\n    text-align: center;\n}\n\n.line-top {\n    top: 16px;\n    transition: 0.4s ease;\n}\n\n.line-middle {\n    top: 26px;\n    transition: 0.4s ease;\n}\n\n.line-bottom {\n    top: 36px;\n    transition: 0.4s ease;\n}\n\n.rotate-top {\n    transform: translateY(10px) rotate(-45deg);\n    transition: 0.4s ease;\n}\n\n.rotate-middle {\n    transition: 0.4s ease;\n    opacity: 0;\n}\n\n.rotate-bottom {\n    transform: translateY(-10px) rotate(45deg);\n    transition: 0.4s ease;\n}\n\n.header-menu-enter-active, .header-menu-leave-active {\n    opacity: 0.4;\n}\n\n.header-menu-enter, .header-menu-leave-to {\n    opacity: 0;\n}\n\n.header-menu-leave, .header-menu-enter-to {\n    opacity: 1;\n}\n\n.header-menu {\n    background: #FFC7AF;\n    z-index: 20;\n    padding: 2rem 1rem;\n    position: fixed;\n    width: 100Vh;\n    height: 80rem;\n    top: 0;\n    left: 0;\n}\n\n.header-inner {\n    display: flex;\n}\n\n.header-inner p {\n    margin-left: 1rem\n}\n\n.header-inner p:first-letter {\n    color: #FF5F17;\n    font-size: 2rem\n}\n\n\n.header-menu_content {\n    padding: 0;\n    padding-top: 40px;\n}\n\n.heaer-menu_list {\n    padding: 35px;\n    list-style: none;\n    line-height: 1;\n    font-size: 1.5rem;\n}\n\n\n\nimg {\n    width: 2.2rem;\n    height: 2.2rem;\n    display: inline-block;\n    \n}\n\n.header {\n    width: 80%;\n    margin: auto;\n    padding: 1rem 0 1rem;\n    display: flex;\n    justify-content: space-between;\n    \n}\n\n.header-title {\n    align-items: center;\n    font-size: 3.5rem;\n    color: #FF5F17;\n    border-bottom: 2.5px solid #FFF;\n    font-weight:bold;\n}\n\n\n.header-list {\n    display: flex;\n    border-bottom: 2.5px solid #FFF;\n    align-items: center;\n}\n\n.header-list_content:last-child {\n    border-right: 2px dotted #FAEBD7;\n}\n\n.header-list_content {\n    padding-left: 2rem;\n    padding-right: 2rem;\n    text-align: center;\n    border-left: 2px dotted #FAEBD7;\n}\n\n.header-list_content p {\n    margin-bottom: 0.5rem;\n}\n\n.header-list_content:hover {\n    background-image: url('/images/select.png');\n    background-size: 70px;\n    background-repeat: no-repeat;\n    background-position: 50% 50%;\n}\n\n@media screen and (max-width: 1024px) {\n    .header-title_resp {\n        position: fixed; \n        top: 0;\n        left: 40%;\n    }\n}\n\n@media screen and (max-width: 480px) {\n    .header-title_resp {\n        position: fixed; \n        top: 8px;\n        left: 28%;\n        font-size: 2.5rem;\n    }\n}\n\n\n</style>"],"sourceRoot":""}]);
