y[data-v-5921f65e]{margin-top:5rem}.show-content[data-v-5921f65e]{width:90%}.show-content[data-v-5921f65e]:after{top:120px}.show-content_mycard[data-v-5921f65e],.show-content_othercard[data-v-5921f65e]{height:150px}.show-content_card-image[data-v-5921f65e]{width:25%}.show-content_card-image img[data-v-5921f65e]{width:100%}.show-content_card-body[data-v-5921f65e]{margin:0 0 0 1rem;width:70%}.content-main[data-v-5921f65e]{margin:0 0 0 .7rem}.title[data-v-5921f65e]{margin-top:.6rem}.author[data-v-5921f65e],.title[data-v-5921f65e]{font-size:.8rem}}", "",{"version":3,"sources":["webpack://./resources/js/components/BookShowComponent.vue"],"names":[],"mappings":"AAoIA,uBACA,oIACA,CAGA,0BASA,kBAAA,CAHA,qBAAA,CAEA,YAAA,CAHA,WAAA,CAKA,sBAAA,CAPA,MAAA,CAFA,cAAA,CACA,KAAA,CAEA,UAAA,CAGA,UAKA,CAGA,+BAGA,kBAAA,CACA,oCAAA,CAHA,kBAAA,CAIA,sBAAA,CAHA,SAIA,CAEA,qCAQA,kBAAA,CANA,UAAA,CAGA,WAAA,CAKA,SAAA,CAHA,WAAA,CADA,UAAA,CALA,iBAAA,CAUA,UAAA,CARA,SAAA,CAMA,uBAAA,CALA,SAQA,CAIA,2BAEA,kBAAA,CAGA,wBAAA,CACA,kBAAA,CAFA,UAAA,CAOA,cAAA,CAVA,oBAAA,CAMA,qBAAA,CAJA,mBAKA,CAOA,6CAIA,kBAAA,CACA,aAAA,CAEA,oBAAA,CADA,eAAA,CAHA,aAAA,CADA,mBAAA,CADA,iBAOA,CAEA,mDAMA,6BAAA,CAAA,wBAAA,CAJA,UAAA,CAMA,QAAA,CAJA,SAAA,CAHA,iBAAA,CAEA,QAAA,CAIA,OAEA,CAEA,sCAIA,kBAAA,CAKA,sBAAA,CADA,6BAAA,CAPA,YAAA,CAEA,YAAA,CAIA,qBAAA,CAFA,mBAAA,CAHA,UAQA,CAKA,gDAIA,kBAAA,CACA,aAAA,CAEA,oBAAA,CADA,eAAA,CAHA,aAAA,CADA,mBAAA,CADA,iBAOA,CAEA,sDAMA,6BAAA,CAAA,wBAAA,CAJA,UAAA,CAMA,QAAA,CAJA,SAAA,CAHA,iBAAA,CAEA,QAAA,CAIA,OAEA,CAEA,yCAIA,kBAAA,CAKA,sBAAA,CADA,6BAAA,CAPA,YAAA,CAEA,YAAA,CAIA,qBAAA,CAFA,mBAAA,CAHA,UAQA,CAGA,sCACA,YAAA,CACA,6BACA,CAGA,4CAGA,cAAA,CADA,eAAA,CAEA,oBACA,CAIA,oCACA,wBAAA,CAIA,wBAAA,CAGA,iBAAA,CADA,mCAAA,CALA,UAAA,CAEA,eAAA,CADA,cAAA,CAGA,uBAGA,CACA,mCACA,wBAAA,CACA,iLACA,CACA,iCAAA,CACA,gBAAA,CACA,uBACA,CAEA,sCACA,YAAA,CACA,qBACA,CACA,wCACA,iBACA,CAEA,2CACA,eAAA,CAGA,qBAAA,CACA,kBAAA,CAFA,UAAA,CADA,mBAIA,CAEA,6CACA,eAAA,CAGA,qBAAA,CACA,kBAAA,CAFA,UAAA,CADA,mBAIA,CAEA,mDACA,cACA,CAEA,0CACA,SACA,CAEA,8CAEA,WAAA,CADA,UAEA,CAEA,yCACA,oBAAA,CACA,SACA,CAGA,+IAKA,6BAAA,CAFA,YAAA,CACA,gBAEA,CAEA,+BAEA,gBAAA,CADA,SAEA,CAEA,wBACA,iBACA,CAEA,0EACA,gBAAA,CACA,gBACA,CAEA,sBACA,gBAAA,CACA,SACA,CAGA,qCACA,sBACA,eACA,CAGA,0EAEA,cAAA,CADA,aAEA,CAEA,sBACA,cACA,CACA,CAEA,oCACA,sBACA,eACA,CAGA,+BACA,SACA,CAEA,qCACA,SACA,CAEA,+EACA,YACA,CAEA,0CACA,SACA,CAEA,8CACA,UACA,CAEA,yCAEA,iBAAA,CACA,SACA,CAEA,+BAEA,kBACA,CAEA,wBACA,gBACA,CAEA,iDACA,eACA,CAEA","sourcesContent":["<template>\n<div class=\"body\">\n    <div v-show=\"isLoading\" class=\"loading\">   \n        <vue-loaders v-show='isLoading'  name =\"ball-spin-fade-loader\" color=\"#FF8856\" scale=\"3\"></vue-loaders>\n    </div>\n    <div class=\"show-content\" v-for=\"item in items\" :key=\"item.id\">\n        <div class=\"show-content-header\">\n            <p class=\"show-content-header_title\">感想</p>\n            <div :class=\"[ checkId(item.user_id) ? 'show-content-header_myname': 'show-content-header_othername']\">\n                <p class=\"show-content-header_name-main\">ユーザー名: {{item.user_name}}</p>\n            </div>\n        </div>\n        <div class=\"show-content_note\">\n        <div class=\"show-content_sen\">\n        <p>{{item.report}}</p>\n        </div>\n        </div>\n        <div class=\"show-content_footer\" >\n            <router-link class=\"show-content_footer-edit\" :to=\"`/book/edit/${item.id}`\" v-if=\"checkId(item.user_id)\">編集する</router-link>\n            <p class=\"show-content_footer-delete\"  @click=\"dataDelete(item.id)\" v-if=\"checkId(item.user_id)\">削除する</p>\n        </div>\n        <div :class=\"[checkId(item.user_id) ? 'show-content_mycard' : 'show-content_othercard']\">\n            <div class=\"show-content_card-image\">\n            <img v-bind:src=\"item.imge\">\n            </div>\n            <div class=\"show-content_card-body\">\n                <div class=\"show-content_card-body_title\">\n                    <p class=\"tag\">タイ<br>トル</p>\n                    <p class=\"content-main title\" >{{item.title}}</p>\n                </div>\n                <div class=\"show-content_card-body_author\">\n                    <p class=\"tag\">著者</p>\n                    <p class=\"content-main author\">{{item.author}}</p>\n                </div>\n                <div class=\"show-content_card-body_rating\" >\n                    <p class=\"tag\">評価</p>\n                    <star-rating class=\"content-main rating\"  v-model=\"item.rating\" v-bind:increment=\"0.5\" :read-only=\"true\" :star-size=29></star-rating>\n                </div>\n            </div>\n        </div>\n    </div>\n    <a class=\"back_btn\" @click=\"$router.back()\">戻る</a>\n</div>    \n</template>\n\n<script>\nexport default ({\n    props:['bookId','user'],\n    data(){\n        return {\n            items:'',\n            isLoading: true,\n            resp: false,\n        }\n    },\n    methods:{\n\n    //詳細データ取得    \n        getData(){\n            const baseUrl = process.env.MIX_API_URL;\n            const id = this.bookId\n            \n            axios.get(`${baseUrl}/`+id)\n            .then((res) => {\n                this.items = res.data;\n                this.isLoading = false;\n                console.log(this.res);\n            })\n            .catch((error) => {\n                console.log(error);\n            })\n        },\n\n    //ログインユーザー確認    \n        checkId(user_id){\n            if(this.user.id == user_id){\n                return true;\n                \n            }\n            return false;\n            \n        },\n\n     //投稿削除処理   \n        dataDelete(id){\n            const baseUrl = process.env.MIX_API_URL;\n            this.$swal({\n                title: \"確認\",\n                text: '本当に削除してもよろしいですか？',\n                icon: \"warning\",\n                buttons: true,\n                dangerMde: true,\n            })\n            .then((willDelete) => {\n                if(willDelete) {\n                    axios.delete(`${baseUrl}/` +id)\n                    .then((res) => {\n                    console.log(res);\n                    this.$swal('削除しました!');\n                    this.$router.push({name:'book.home'});\n                })\n                    .catch((error) => {\n                    console.log(error);\n                    alert('削除失敗です');\n                });\n                }\n            });\n        },\n\n    //画面検知    \n        handleResize(){\n            if(window.innerWidth <= 480){\n                this.resp = true;\n            }else if(window.innerWidth <= 1024) {\n                this.resp = true;\n            }else {\n                this.resp = false;\n            }\n            \n        }\n        \n    },\n\n    created(){\n        this.getData();\n    }\n})\n</script>\n\n<style scoped>\n\n\n.body {\n    font-family: 'Avenir','Arial Rounded MT Bold','Comic Sans MS','Hannotate SC','Hiragino Kaku Gothic ProN','ヒラギノ角ゴ ProN W3','Yu Gothic UI',sans-serif;\n}\n\n/* ローディングぐるぐる */\n.loading {\n    position:fixed;\n    top:0;\n    left:0;\n    width: 100%;\n    height: 100%;\n    background-color: rgba(255,255,255,1);\n    z-index: 80;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n\n}\n\n\n.show-content {\n        margin: 3rem auto 0;\n        width: 70%;\n        background :#FFF6e6;\n        box-shadow: 0 2px 3px rgba(0,0,0,.22);\n        padding: 2.2rem 0.8rem 0 0.8rem;\n}\n\n.show-content:after {\n        position: absolute;\n        content: '';\n        top: 150px;\n        width: 30%;\n        height: 35px;\n        opacity: 0.3;\n        margin: auto ;\n        background: rgb(177, 255, 133);\n        transform: rotate(-2deg);\n        left: 10px;\n        right: 10px;\n}\n\n/* 戻るボタン */\n\n.back_btn {\n    display: inline-block;\n    background: #FFC7AF;\n    padding: 1rem 1.5rem;\n    color: #000;\n    border: 1px solid #FFC7AF;\n    border-radius: 15px;\n    margin: 5rem 0  0 10rem;\n}\n\n.back_btn {\n    cursor: pointer;\n}\n\n/* 自分の投稿の時のスタイル */\n.show-content-header_myname {\n    position: relative;\n    padding: 0.5rem 0.7rem;\n    margin: 2rem 0;\n    background: #FFDBC9;\n    color: #FF9872;\n    font-weight: bold;\n    display: inline-block;\n}\n\n.show-content-header_myname:after {\n    position: absolute;\n    content: '';\n    top: 100%;\n    left: 30px;\n    border: 15px solid transparent;\n    border-top: 15px solid #FFDBC9;\n    width: 0;\n    height: 0;\n}\n\n.show-content_mycard {\n    display: flex;\n    width: 100%;\n    height: 180px;\n    background: #FFDBC9;\n    padding: 0.2rem 0.5rem;\n    margin: auto;\n    margin-top: 2rem;\n    box-shadow: 0px 0px 0px 10px #FFDBC9;\n    border: 2px dashed #fff;\n}\n/* 終わり */\n\n\n/* 他の人の投稿の時のスタイル */\n.show-content-header_othername {\n    position: relative;\n    padding: 0.5rem 0.7rem;\n    margin: 2rem 0;\n    background: #e6f4ff;\n    color: #5c98d4;\n    font-weight: bold;\n    display: inline-block;\n}\n\n.show-content-header_othername:after {\n    position: absolute;\n    content: '';\n    top: 100%;\n    left: 30px;\n    border: 15px solid transparent;\n    border-top: 15px solid #e6f4ff;\n    width: 0;\n    height: 0;\n}\n\n.show-content_othercard {\n    display: flex;\n    width: 100%;\n    height: 180px;\n    background: #d6edff;\n    padding: 0.2rem 0.5rem;\n    margin: auto;\n    margin-top: 2rem;\n    box-shadow: 0px 0px 0px 10px #d6edff;\n    border: 2px dashed #fff;\n}\n/* 終わり */\n\n.show-content-header {\n    display: flex;\n    justify-content: space-between;\n}\n\n\n.show-content-header_title {\n\n    font-weight: bold;\n    font-size: 3rem;\n    letter-spacing: 0.5rem;\n}\n\n\n\n.show-content_note {\n    background-color: #fff6e6;\n    color: #000;\n    margin: 0.5rem 0;\n    font-weight: bold;\n    border: solid 1px #ee8992;\n    padding: 0.3rem 1rem 2rem 1rem;\n    box-shadow:2px 2px 0 rgba(0,0,0,.1);\n    border-radius: 7px;\n}\n.show-content_sen {\n    background-color: #fff6e6;\n    background-image:\n    linear-gradient(90deg, rgba(255, 246, 230, 0) 0%, rgba(255, 246, 230, 0) 50%, #fff6e6 0%, #fff6e6 100%), linear-gradient(180deg, rgba(237, 119, 128, 0) 0%, rgba(237, 119, 128, 0) 95%, #ed7780 100%);\n    background-size: 8px 100%,100% 2em;\n    line-height: 2rem;\n    padding: 2rem 1rem 0.2rem 1rem;\n}\n\n.show-content_footer {\n    display: flex;\n    justify-content: right;\n}\n.show-content_footer p {\n    margin-left: 0.6rem;\n}\n\n.show-content_footer-edit {\n    background: #fff;\n    padding: 0.3rem 0.8rem;\n    color: #BBBBBB;\n    border: 1px solid #BBBBBB;\n    border-radius: 15px;\n}\n\n.show-content_footer-delete {\n    background: #555555;\n    padding: 0.3rem 0.8rem;\n    color: #fff;\n    border: 1px solid #555555;\n    border-radius: 15px;\n}\n\n.show-content_footer-delete:hover {\n    cursor: pointer;\n}\n\n.show-content_card-image {\n    width: 20%;\n}\n\n.show-content_card-image img {\n    width: 100%;\n    height: 100%;\n}\n\n.show-content_card-body {\n    margin: 1rem 0 0 3rem;\n    width: 75%;\n}\n\n\n.show-content_card-body_title,\n.show-content_card-body_author,\n.show-content_card-body_rating {\n    display: flex;\n    margin-top: 0.6rem;\n    border-bottom: 1px dashed #fff;\n}\n\n.content-main {\n    width: 80%;\n    margin-left: 5rem;\n}\n\n.title {\n    padding-top: 0.5rem;\n}\n\n.title,.author,.rating {\n    font-size: 1.2rem;\n    margin-left :4rem;\n}\n\n.tag  {\n    font-size: 1.3rem;\n    width: 20%;\n}\n\n\n@media screen and (max-width: 1024px) {\n    body {\n        margin-top: 5rem;\n    }\n    \n\n    .title,.author,.rating {\n        margin-left: 0;\n        font-size: 1rem;\n    }  \n    \n    .tag {\n        font-size: 1rem;\n    }\n}\n\n@media screen and (max-width:800px) {\n    body {\n        margin-top: 5rem;\n    }\n\n\n    .show-content {\n        width: 90%;\n    }\n\n    .show-content:after {\n        top: 120px;\n    } \n    \n    .show-content_mycard, .show-content_othercard {\n        height: 150px;\n    }\n\n    .show-content_card-image {\n        width: 25%;\n    }\n\n    .show-content_card-image img {\n        width: 100%;\n    }\n\n    .show-content_card-body {\n        margin: 0;\n        margin-left: 1rem;\n        width: 70%;\n    }\n\n    .content-main {\n        margin: 0;\n        margin-left: 0.7rem;\n    }\n\n    .title {\n        margin-top: 0.6rem;\n    }\n\n    .title,.author {\n        font-size: 0.8rem;\n    }\n\n}\n</style>"],"sourceRoot":""}]);
