rent transparent;border-width:15px 20px 0 0;left:0;top:0}.btn[data-v-34042461]:after,.btn[data-v-34042461]:before{border-style:solid;content:\"\";height:0;position:absolute;width:0}.btn[data-v-34042461]:after{border-color:transparent transparent #efefef;border-width:0 0 20px 15px;box-shadow:1px 1px 1px 0 rgba(0,0,0,.15);left:5px;top:-6px;transform:rotate(16deg)}.btn[data-v-34042461]:hover{background-color:#e6de6b}.btn span[data-v-34042461]{letter-spacing:.05em;padding-left:16px;position:relative}.btn span[data-v-34042461]:before{border:0;border-right:2px solid #fff;border-top:2px solid #fff;content:\"\";height:6px;left:0;margin-top:-4px;position:absolute;top:50%;transform:rotate(45deg);width:6px}.responsive_other_inner[data-v-34042461]{background-color:#d6edff;border:2px dashed #fff;border-radius:10px;box-shadow:0 0 0 10px #d6edff;color:#000;display:flex;font-size:30px;height:190px;margin:5rem auto 4rem;padding:.2rem .5rem;width:75%}.responsive_other_inner[data-v-34042461]:hover{box-shadow:0 7px 14px #32325d,0 3px 6px rgba(0,0,0,.08);transform:translateY(-3px);transition:all .5s}.responsive_other_inner-header[data-v-34042461]{height:100%;width:40%}.responsive_other_inner-header img[data-v-34042461]{height:100%;width:100%}.responsive_other_body[data-v-34042461]{margin-left:.6rem;width:50%}.responsive_other_body-name[data-v-34042461]{display:inline-block;font-size:.8rem;margin-top:.5rem}.responsive_other_body-title[data-v-34042461]{font-size:1rem;font-weight:700;margin-left:.3rem}.responsive_other_body-author[data-v-34042461]{font-size:.8rem;text-align:right}.responsive_other_body-footer[data-v-34042461]{display:flex;font-size:.7rem;margin-top:.6rem}@media screen and (max-width:1024px){body[data-v-34042461]{margin-top:5rem}.responsive_other_inner-header img[data-v-34042461]{width:70%}.responsive_other_body-title[data-v-34042461]{font-size:1.1rem;margin-top:1rem}.responsive_other_body-author[data-v-34042461]{font-size:.9rem}.responsive_other_body-footer[data-v-34042461