CADA,eAEA,CAEA,+CACA,eACA,CAEA,+CACA,eACA,CACA,CAEA,oCACA,sBACA,eACA,CAEA,8CAEA,cAAA,CADA,gBAEA,CAEA,+CACA,eACA,CAEA,+CACA,eACA,CACA","sourcesContent":["<template>\n    <div class=\"body\">\n        <div class=\"top-btn\" @click=\"scrollTop\">\n            <i class=\"fas fa-chevron-up Page-Btn-Icon\"></i>\n        </div>\n        <div v-show=\"isLoading\" class=\"loading\">   \n            <vue-loaders v-show='isLoading'  name =\"ball-spin-fade-loader\" color=\"#FF8856\" scale=\"3\"></vue-loaders>\n        </div>\n        <div v-show=\"!resp\" class=\"other\">\n            <p class=\"heading\">みんなの投稿 </p>\n            <p class=\"not_data\" v-show=\"!checkOtherData\">投稿がありません</p>\n            <div v-show=\"checkOtherData\">\n                <div v-for=\"other in others\" :key=\"other.id\">\n                    <div class =\"other_data\" >\n                        <div class=\"other_data-image\">\n                            <img  v-bind:src=\"other.imge\" alt=\"\">\n                        </div>     \n                        <div class=\"other_data_body\"> \n                            <div class=\"other_data_body-title\">\n                                <p class=\"tag\">タイ<br>トル</p>\n                                <p class=\"content-main title\">{{other.title}}</p> \n                            </div>\n                            <div class=\"other_data_body-author\" >\n                                <p class=\"tag\">著者</p>\n                                <p class=\"content-main author\">作 {{other.author}}</p>\n                            </div>\n                            <div class=\"other_data_body-rating\">\n                                <p class=\"tag\">評価</p>\n                                <p class=\"content-main rating\">{{other.rating}}</p>\n                            </div>\n                            <div class=\"other_data_body-footer\">\n                                <p class=\"tag\">{{timeStamp(other.created_at)}}</p>\n                                <p class=\"other_data_body-footer-name name\">ユーザー名:{{other.user_name}}</p>\n                                <router-link class=\"content-main btn\"  :to=\"`/book/show/${other.id}`\">詳細を見る！</router-link>\n                            </div>    \n                        </div>\n                    </div>\n                </div>\n            </div>    \n        </div>\n        <div>\n            <div v-show=\"resp\" class=\"responsive\">\n                <p class=\"heading\">みんなの投稿</p>\n                <p class=\"not_data\" v-show=\"!checkOtherData\">投稿がありません</p>\n                <div v-show=\"checkOtherData\">\n                    <div class=\"responsive_other\" v-for=\"other in others\" :key=\"other.id\">\n                        <router-link class=\"responsive_other-show\" :to=\"`/book/show/${other.id}`\">\n                            <div class=\"responsive_other_inner\">\n                                <div class=\"responsive_other_inner-header\">\n                                    <img v-bind:src=\"other.imge\">\n                                </div>     \n                                <div class=\"responsive_other_body\"> \n                                    <p class=\"responsive_other_body-title\">{{other.title}}</p> \n                                    <p class=\"responsive_other_body-author\">作 {{other.author}}</p>\n                                    <div class=\"responsive_other_body-footer\">\n                                        <star-rating v-model=\"other.rating\" :read-only=\"true\" :star-size=20 ></star-rating>\n                                    </div>\n                                    <p class=\"responsive_other_body-name name\">ユーザー名:{{other.user_name}}</p>\n                                </div>\n                            </div>\n                        </router-link>\n                    </div>\n                </div>        \n            </div>  \n        </div>\n    </div>\n</template>\n\n<script>\nexport default {\n    props:['user'],\n    data(){\n        return{\n            others:'',\n            isLoading: true,\n            resp: false,\n            checkOtherData: '',\n        }\n    },\n\n    methods:{\n\n    // データ取得    \n        async getOtherData(){\n            const baseUrl = process.env.MIX_API_URL;\n            await axios.get(`${baseUrl}/other`,{params:{user_id:this.user.id}})\n            .then((res)=>{\n                this.others = res.data.data\n                if(!this.others.length == 0) {\n                    this.checkOtherData = true;\n                } else {\n                    this.checkOtherData = false;\n                }\n                this.isLoading = false;\n                console.log(this.others);\n            })\n            .catch((error) => {\n                console.log(error);\n                alert('失敗です');\n            })\n        },\n\n    // 画面幅検知\n        handleResize(){\n            if(window.innerWidth <= 1024){\n                this.resp = true;\n            }else {\n                this.resp = false;\n            }\n        },\n\n    // topに戻る処理\n        scrollTop(){\n            window.scrollTo({\n                top: 0,\n                behavior: 'smooth'\n            });\n        }\n    },\n\n    computed:{\n            timeStamp:function(){\n                return function(value) {\n                    return value.substring(0,10);\n                }\n            }\n        },\n\n    created(){\n        window.addEventListener('resize',this.handleResize);\n        this.handleResize();\n        this.getOtherData();\n    }\n}\n</script>\n\n<style scoped>\n\n.body {\n    font-family: 'Avenir','Arial Rounded MT Bold','Comic Sans MS','Hannotate SC','Hiragino Kaku Gothic ProN','ヒラギノ角ゴ ProN W3','Yu Gothic UI',sans-serif;\n}\n\n/* topに戻る */\n.top-btn {\n    color: #fff;\n    position: fixed;\n    right: 14px;\n    bottom: 35px;\n    width: 32px;\n    height: 32px;\n    line-height: 32px;\n    text-align: center;\n    border-radius: 50%;\n    background: #5bc8ac;\n}\n\n/* ローディングぐるぐる */\n.loading {\n    position:fixed;\n    top:0;\n    left:0;\n    width: 100%;\n    height: 100%;\n    background-color: rgba(255,255,255,1);\n    z-index: 80;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n\n}\n\n/* 見出し */\n.heading {\n    display: inline-block;\n    font-weight: bold;\n    font-size: 1.5rem;\n    border-bottom: 2px solid #fff;\n}\n\n.heading:first-letter {\n    color: #FF5F17;\n    font-size: 3rem;\n}\n\n/* みんなのデータ */\n.other_data,.other_data_body-title, \n.other_data_body-author,.other_data_body-rating\n{\n    display: flex;\n    margin-top: 0.6rem;\n    border-bottom: 1px dashed #fff;\n\n}\n\n.other_data_body-footer {\n    display: flex;\n    margin-top: 0.6rem;\n    justify-content: space-between;\n\n}\n\n.other {\n    width: 100%;\n}\n\n.other_data {\n    width: 900px;\n    height: 210px;\n    background: #d6edff;\n    padding: 0.2rem 0.5rem;\n    margin: auto;\n    margin-top: 6rem;\n    box-shadow: 0px 0px 0px 10px #d6edff;\n    border: 2px dashed #fff;\n}\n\n.other_data_body {\n    margin: 1rem 0 0 6rem;\n    width: 70%;\n}\n\n.other_data-image {\n    width: 15%;\n}\n\n.other_data-image img {\n    width: 100%;\n    height: 100%;\n}\n\n.tag {\n    font-size: 1rem;\n    width: 20%;\n}\n\n.content-main {\n    font-size: 2.3vmin;\n    margin-left: 5rem;\n}\n\n.title,.author, .rating {\n    margin-left: 7rem;\n}\n\n.name {\n    background:linear-gradient(transparent 30%, #FFCCCC 60%);\n}\n\n.btn {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    width: 30%;\n    height: 35px;\n    background-color: #FF5F17;\n    box-sizing: border-box;\n    color: #fff;\n    font-size: 16px;\n    letter-spacing: 0.1em;\n    line-height: 2.0;\n    text-decoration: none;\n    transition-duration: 0.3s;\n    position: relative;\n}\n\n.btn:before {\n    content: \"\";\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 0;\n    height: 0;\n    border-style: solid;\n    border-width: 15px 20px 0 0;\n    border-color: #ffffff transparent transparent transparent;\n}\n\n.btn:after {\n    content: \"\";\n    position: absolute;\n    top: -6px;\n    left: 5px;\n    width: 0;\n    height: 0;\n    border-style: solid;\n    border-width: 0 0 20px 15px;\n    border-color: transparent transparent #efefef transparent;\n    box-shadow: 1px 1px 1px 0px rgba(0, 0, 0, 0.15);\n    transform: rotate(16deg);\n}\n\n.btn:hover {\n    background-color: #e6de6b;\n}\n\n.btn span {\n    position: relative;\n    padding-left: 16px;\n    letter-spacing: 0.05em;\n}\n\n.btn span:before {\n    content: '';\n    width: 6px;\n    height: 6px;\n    border: 0;\n    border-top: solid 2px #fff;\n    border-right: solid 2px #fff;\n    transform: rotate(45deg);\n    position: absolute;\n    top: 50%;\n    left: 0;\n    margin-top: -4px;\n}\n\n/* otherレスポンシブ */\n.responsive_other_inner {\n    height: 190px;\n    width: 75%;\n    margin: 5rem auto 4rem;\n    padding: 0.2rem 0.5rem;\n    background-color: #d6edff; \n    display: flex; \n    color: #000; \n    box-shadow: 0px 0px 0px 10px #d6edff;\n    border: 2px dashed #fff;\n    font-size: 30px; \n    border-radius: 10px;\n}\n\n.responsive_other_inner:hover {\n    transform: translateY(-3px);\n    box-shadow: 0 7px 14px rgba(50,50,93,1), 0 3px 6px rgba(0,0,0,.08);\n    transition: all .5s;\n}\n\n.responsive_other_inner-header {\n    width: 40%;\n    height: 100%;\n    /* padding: 0.2rem 0.5rem; */\n}\n\n.responsive_other_inner-header img {\n    width: 100%;\n    height: 100%;\n}\n\n.responsive_other_body {\n    width: 50%;\n    margin-left: 0.6rem;\n}\n\n.responsive_other_body-name {\n    display: inline-block;\n    font-size: 0.8rem;\n    margin-top: 0.5rem;\n}\n\n.responsive_other_body-title {\n    font-size: 1rem;\n    font-weight: bold;\n    margin-left: 0.3rem;\n}\n\n.responsive_other_body-author {\n    font-size: 0.8rem;\n    text-align: right;\n}\n\n.responsive_other_body-footer {\n    display: flex;\n    font-size: 0.7rem; \n    margin-top: 0.6rem;\n}\n\n@media screen and (max-width: 1024px) {\n    body {\n        margin-top: 5rem;\n    }\n\n    .responsive_other_inner-header img {\n        width: 70%;\n    }\n\n    .responsive_other_body-title {\n        margin-top: 1rem;\n        font-size: 1.1rem\n    }\n\n    .responsive_other_body-author {\n        font-size: 0.9rem;\n    }\n\n    .responsive_other_body-footer {\n        font-size: 0.8rem; \n    }\n}\n\n@media screen and (max-width: 600px) {\n    body {\n        margin-top: 5rem;\n    }\n\n    .responsive_other_body-title {\n        margin-top: 0.3rem;\n        font-size: 1rem\n    }\n\n    .responsive_other_body-author {\n        font-size: 0.8rem;\n    }\n\n    .responsive_ohter_body-footer {\n        font-size: 0.7rem; \n    }\n}\n\n</style>"],"sourceRoot":""}]);
