en and (max-width:1024px){body[data-v-fe9a3f82]{margin-top:5rem}}", "",{"version":3,"sources":["webpack://./resources/js/components/BookEditComponent.vue"],"names":[],"mappings":"AA4HA,uBACA,oIACA,CAGA,0BASA,kBAAA,CAHA,qBAAA,CAEA,YAAA,CAHA,WAAA,CAKA,sBAAA,CAPA,MAAA,CAFA,cAAA,CACA,KAAA,CAEA,UAAA,CAGA,UAKA,CAIA,2BAEA,kBAAA,CAGA,wBAAA,CACA,kBAAA,CAFA,UAAA,CAHA,oBAAA,CAMA,qBAAA,CAJA,mBAKA,CAGA,iDACA,UACA,CAEA,0BACA,YACA,CAEA,wBACA,UACA,CAEA,yBAEA,cAAA,CADA,eAEA,CAEA,wBAGA,kBAAA,CACA,oCAAA,CAHA,kBAAA,CAIA,oBAAA,CAHA,SAIA,CAEA,8BAQA,kBAAA,CANA,UAAA,CAGA,WAAA,CAKA,SAAA,CAHA,0BAAA,CADA,UAAA,CALA,iBAAA,CAUA,UAAA,CARA,SAAA,CAMA,uBAAA,CALA,SAQA,CAEA,gCAEA,WAAA,CADA,SAEA,CAEA,uCACA,YAAA,CACA,6BACA,CAEA,6CAGA,4BAAA,CAFA,cAAA,CACA,eAEA,CAEA,qCACA,eACA,CAGA,4BAGA,qBAAA,CACA,kBAAA,CAHA,aAAA,CAIA,gBAAA,CAHA,kBAIA,CAEA,2BAEA,kBAAA,CADA,UAEA,CAKA,oCACA,sBACA,eACA,CAGA,wBAEA,kBAAA,CADA,SAGA,CAEA,8BAQA,kBAAA,CANA,UAAA,CAGA,WAAA,CAEA,sBAAA,CADA,UAAA,CALA,iBAAA,CAEA,QAAA,CAMA,uBAAA,CALA,SAMA,CAEA,6CACA,WAAA,CACA,SACA,CAEA,gCACA,UACA,CAEA,uCAEA,cAAA,CADA,eAEA,CACA,CAEA,qCACA,sBACA,eACA,CACA","sourcesContent":["<template>\n    <div class=\"body\">\n        <div v-show=\"isLoading\" class=\"loading\">   \n            <vue-loaders v-show='isLoading' name =\"ball-spin-fade-loader\" color=\"#FF8856\" scale=\"3\"></vue-loaders>\n        </div>\n        <div class=\"input\">\n            <div class=\"input-content\">\n                <div v-for=\"error in errors\" :key=\"error.id\">\n                    <div class=\"error\"><p>{{error}}</p></div>\n                </div>\n                <div class=\"input-content_header\">\n                    <h2 class=\"input-content_header-title\">編集</h2>\n                </div>\n                <validation-observer tag=\"form\" ref=\"obs\" v-slot=\"ObserverProps\" v-on:submit.prevent=\"submit\">  \n                    <div class=\"input-content_body\">\n                        <validation-provider v-slot=\"ProviderProps\" rules=\"required|max:50\">\n                            <p>タイトル</p>\n                            <input type=\"text\" v-model=\"item.title\" name=\"タイトル\">\n                            <div class=\"error\">{{ProviderProps.errors[0]}}</div>\n                        </validation-provider>\n                    </div>\n                    <div class=\"input-content_body\">\n                        <validation-provider v-slot=\"ProviderProps\" rules=\"required|max:30\">\n                            <p>著者</p>\n                            <input type=\"text\"  v-model=\"item.author\" name=\"著者\">\n                            <div class=\"error\">{{ProviderProps.errors[0]}}</div>\n                        </validation-provider>\n                    </div>\n                    <div class=\"input-content_body\">\n                        <p class=\"input_rating-title\">評価</p>\n                        <star-rating v-model=\"rating\" v-bind:increment=\"0.5\"></star-rating>\n                    </div>\n                    <div class=\"input-content_body\">\n                        <validation-provider v-slot=\"ProviderProps\" rules=\"required|max:500\">\n                            <p>感想</p>\n                            <textarea v-model=\"item.report\" name=\"感想\"></textarea>\n                            <div class=\"error\">{{ProviderProps.errors[0]}}</div>\n                        </validation-provider>\n                    </div>\n                    <button class=\"input-btn\" type=\"submit\" :disabled=\"ObserverProps.invalid\">更新</button>\n                </validation-observer>\n            </div>\n        </div>\n        <a class=\"back_btn\" @click=\"$router.back()\">戻る</a>\n    </div>    \n</template>\n\n<script>\nexport default ({\n    props:['user','bookId'],\n    data(){\n        return{\n            item:'',\n            rating:'',\n            errors: {},\n            isLoading: true,\n        }\n    },\n    methods:{\n\n    // ログインユーザーチェック処理    \n        checkId(id){\n            if(this.user_id !== id) {\n                this.$router.push({name: 'book.home'});\n            }\n        },\n\n    // 編集データ取得    \n        getData() {\n            const baseUrl = process.env.MIX_API_URL;\n            const id = this.bookId;\n            axios.get(baseUrl+id)\n            .then((res) => {\n                console.log(res.data.data);\n                this.item = res.data.data;\n                this.rating = res.data.data.rating;\n                this.isLoading =false;\n            })\n            .catch((error) => {\n                console.log(error);\n                alert('データ取得に失敗しました')\n            })\n        },\n\n    // 編集内容データベース登録処理\n        submit(){\n            const baseUrl = process.env.MIX_API_URL;\n            this.$swal({\n                title: \"確認\",\n                text: 'こちらの内容で更新しますか？',\n                icon: \"info\",\n                buttons: true,\n                dangerMode: true,\n            }).then((willDelete) =>{\n                if(willDelete){\n                    this.item.rating = this.rating;\n                    axios.put(baseUrl+this.bookId,this.item)\n                    .then((res) => {\n                    console.log(res);\n                    this.$swal('更新に成功しました',{\n                        icon: \"success\"\n                    });\n                    this.$router.push({name: 'book.home'});\n                }).catch((error) => {\n                    console.log(error);\n                    this.$swal('更新に失敗しました',{\n                        icon: \"error\"\n                    });\n                })\n                }\n            })\n        }\n    },\n    mounted(){\n        this.checkId();\n    },\n    created(){\n        this.getData();\n    }\n})\n</script>\n\n<style scoped>\n\n.body {\n    font-family: 'Avenir','Arial Rounded MT Bold','Comic Sans MS','Hannotate SC','Hiragino Kaku Gothic ProN','ヒラギノ角ゴ ProN W3','Yu Gothic UI',sans-serif;\n}\n\n/* ローディングぐるぐる */\n.loading {\n    position:fixed;\n    top:0;\n    left:0;\n    width: 100%;\n    height: 100%;\n    background-color: rgba(255,255,255,1);\n    z-index: 80;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n\n}\n\n/* 戻るボタン */\n\n.back_btn {\n    display: inline-block;\n    background: #FFC7AF;\n    padding: 1rem 1.5rem;\n    color: #000;\n    border: 1px solid #FFC7AF;\n    border-radius: 15px;\n    margin: 2rem 0  0 10rem;\n}\n\n/* form */\ninput,textarea {\n    width: 100%;\n    }\n\ntextarea {\n    height: 200px;\n}\n\n.error {\n    color: #666666;\n}\n\n.create {\n    font-weight: bold;\n    font-size: 2rem;\n}\n\n.input {\n    margin: 3rem auto 0;\n    width: 70%;\n    background :#FFF6e6;\n    box-shadow: 0 2px 3px rgba(0,0,0,.22);\n    padding: 2.2rem 0.8rem 2.2rem 0.8rem;\n}\n\n.input:after {\n    position: absolute;\n    content: '';\n    top: 185px;\n    width: 30%;\n    height: 35px;\n    opacity: 0.3;\n    margin: -35px auto 10px 35%;\n    background: #db7093;\n    transform: rotate(-2deg);\n    left: 10px;\n    right: 10px;\n}\n\n.input-content {\n    width: 50%;\n    margin: auto;\n}\n\n.input-content_header {\n    display: flex;\n    justify-content: space-between;\n}\n\n.input-content_header-title {\n    font-size: 2rem;\n    font-weight: bold;\n    border-bottom: 2px solid #fff;\n}\n\n.input-content_body {\n    margin-top : 1rem;\n}\n\n\n.input-btn{\n    display: block;\n    padding: 0.5rem 1rem;\n    background-color: #FF6666;\n    border-radius: 10px;\n    margin-left: auto;\n}\n\n:disabled {\n    opacity: 0.3;\n    cursor: not-allowed;\n}\n\n\n\n\n@media screen and (max-width:480px) {\n    body {\n        margin-top: 5rem;\n    }\n\n\n    .input {\n        width:90%;\n        padding: 1rem 0.5rem;\n        \n    }\n\n    .input:after {\n        position: absolute;\n        content: '';\n        top: 20px;\n        width: 50%;\n        height: 35px;\n        opacity: 0.3;\n        margin: -35px auto 10px;\n        background: #db7093;\n        transform: rotate(-2deg);\n    }\n\n    .input-content_header-title {\n        border: none;\n        width: 80%;\n    }\n\n    .input-content {\n        width: 100%;\n    }\n\n    .input-content_header {\n        margin-top: 1rem;\n        font-size: 1rem;\n    }\n}\n\n@media screen and (max-width: 1024px) {\n    body {\n        margin-top: 5rem;\n    }\n}\n\n</style>"],"sourceRoot":""}]);
