)}.btn:hover{background-color:#e6de6b}.btn span{letter-spacing:.05em;padding-left:16px;position:relative}.btn span:before{border:0;border-right:2px solid #fff;border-top:2px solid #fff;content:\"\";height:6px;left:0;margin-top:-4px;position:absolute;top:50%;transform:rotate(45deg);width:6px}.new{text-align:center}.new_btn{background-color:transparent;border:2px solid #900;color:#900;cursor:pointer;display:inline-block;font-size:18px;margin-top:5rem;padding:.5rem 1rem .3rem;position:relative}.new_btn,.new_btn:before{-webkit-animation:fluffy1 3s ease infinite;animation:fluffy1 3s ease infinite;border-radius:5px}.new_btn:before{background-color:#f96;content:\"\";height:100%;left:5px;position:absolute;top:5px;width:100%;z-index:-1}@-webkit-keyframes fluffy1{0%{transform:translateY(0)}5%{transform:translateY(0)}10%{transform:translateY(0)}20%{transform:translateY(-15px)}25%{transform:translateY(0)}30%{transform:translateY(-15px)}50%{transform:translateY(0)}to{transform:translateY(0)}}@keyframes fluffy1{0%{transform:translateY(0)}5%{transform:translateY(0)}10%{transform:translateY(0)}20%{transform:translateY(-15px)}25%{transform:translateY(0)}30%{transform:translateY(-15px)}50%{transform:translateY(0)}to{transform:translateY(0)}}.responsive_my-data_inner{background-color:#ffdbc9;border:2px dashed #fff;border-radius:10px;box-shadow:0 0 0 10px #ffdbc9;color:#000;display:flex;font-size:30px;height:155px;margin:5rem auto 4rem;padding:.2rem .5rem;width:75%}.responsive_my-data_inner:hover{box-shadow:0 7px 14px #32325d,0 3px 6px rgba(0,0,0,.08);transform:translateY(-3px);transition:all .5s}.responsive_my-data_inner-header{height:100%;width:30%}.responsive_my-data_inner-header img{height:100%;width:100%}.responsive_my-data_body{margin-left:.3rem;width:70%}.responsive_my-data_body-title{font-size:1rem;font-weight:700;margin-left:.3rem}.responsive_my-data_body-author{font-size:.8rem;text-align:right}.responsive_my-data_body-footer{display:flex;font-size:.7rem;margin-top:.6rem}@medi