s:center;background-color:#f60;color:#fff;display:flex;font-size:.8rem;justify-content:space-between;margin-right:3rem;margin-top:2rem;padding:1rem 2rem;transition:.3s;width:180px}.list-link_btn[data-v-0253b5f9]:after{border-right:3px solid #333;border-top:3px solid #333;content:\"\";height:7px;transform:rotate(45deg);width:7px}.list-link_btn[data-v-0253b5f9]:hover{background-color:#bbb;text-decoration:none}.responsive[data-v-0253b5f9]{margin-top:5rem}.responsive_my-data_inner[data-v-0253b5f9]{background-color:#ffdbc9;border:2px dashed #fff;border-radius:10px;box-shadow:0 0 0 10px #ffdbc9;color:#000;display:flex;font-size:30px;height:155px;margin:5rem auto 2rem;padding:.2rem .5rem;width:75%}.responsive_my-data_inner[data-v-0253b5f9]:hover{box-shadow:0 7px 14px #32325d,0 3px 6px rgba(0,0,0,.08);transform:translateY(-3px);transition:all .5s}.responsive_my-data_inner-header[data-v-0253b5f9]{height:100%;width:40%}.responsive_my-data_inner-header img[data-v-0253b5f9]{height:100%;width:100%}.responsive_my-data_body[data-v-0253b5f9]{margin-left:.6rem;width:50%}.responsive_my-data_body-title[data-v-0253b5f9]{font-size:1rem;font-weight:700;margin-left:.3rem}.responsive_my-data_body-author[data-v-0253b5f9]{font-size:.8rem;text-align:right}.responsive_my-data_body-footer[data-v-0253b5f9]{display:flex;font-size:.7rem;margin-top:.6rem}@media screen and (max-width:1024px){body[data-v-0253b5f9]{margin-top:5rem}.responsive_my-data_inner-header img[data-v-0253b5f9]{width:70%}.responsive_my-data_body-title[data-v-0253b5f9]{font-size:1.1rem;margin-top:1rem}.responsive_my-data_body-author[data-v-0253b5f9]{font-size:.9rem}.responsive_my-data_body-footer[data-v-0253b5f9]{font-size:.8rem}}@media screen and (max-width:480px){body[data-v-0253b5f9]{margin-top:5rem}.responsive_my-data_body-title[data-v-0253b5f9]{font-size:1rem;margin-top:.3rem}.responsive_my-data_body-author[data-v-0253b5f9]{font-size:.8rem}.responsive_my-data_body-footer[data-v-0253b5f9]{font-size:.7rem}}", "",{"version":3,"