,CAIA,sBAAA,CAEA,kBAAA,CAHA,6BAAA,CADA,UAAA,CADA,YAAA,CAIA,cAAA,CATA,YAAA,CAEA,qBAAA,CACA,mBAAA,CAFA,SAUA,CAEA,iDAEA,uDAAA,CADA,0BAAA,CAEA,kBACA,CAEA,kDAEA,WAAA,CADA,SAGA,CAEA,sDAEA,WAAA,CADA,UAEA,CAEA,0CAEA,iBAAA,CADA,SAEA,CAEA,gDACA,cAAA,CACA,eAAA,CACA,iBACA,CAEA,iDACA,eAAA,CACA,gBACA,CAEA,iDACA,YAAA,CACA,eAAA,CACA,gBACA,CAIA,qCACA,sBACA,eACA,CAEA,sDACA,SACA,CAEA,gDAEA,gBAAA,CADA,eAEA,CAEA,iDACA,eACA,CAEA,iDACA,eACA,CACA,CAEA,oCACA,sBACA,eACA,CAEA,gDAEA,cAAA,CADA,gBAEA,CAEA,iDACA,eACA,CAEA,iDACA,eACA,CACA","sourcesContent":["<template>\n    <div class=\"body\">\n        <div class=\"top-btn\" @click=\"scrollTop\">\n            <i class=\"fas fa-chevron-up Page-Btn-Icon\"></i>\n        </div>\n        <div v-show=\"isLoading\" class=\"loading\">   \n            <vue-loaders v-show='isLoading'  name =\"ball-spin-fade-loader\" color=\"#FF8856\" scale=\"3\"></vue-loaders>\n        </div>\n        <div>\n            <div class=\"other\">\n                <p class=\"heading\">みんなの投稿</p>\n                <p class=\"not_data\" v-show=\"!checkOtherData\">投稿がありません</p>\n                <div v-show=\"checkOtherData\">\n                    <carousel  :autoplay=\"true\"   :per-page=\"page\" :speed=\"3000\" :autoplayTimeout=\"4000\" pagination-color=\"#CCCCCC\">\n                        <slide class=\"slider\" v-for=\"other in others\" :key=\"other.id\">\n                            <router-link class=\"slider-show\" :to=\"`/book/show/${other.id}`\">\n                                <div class=\"slider-inner\">\n                                    <div class=\"slider-header\">\n                                        <img v-bind:src=\"other.imge\">\n                                    </div>     \n                                    <div class=\"slider-body\"> \n                                        <p class=\"slider-title\">{{other.title}}</p> \n                                        <p class=\"slider-author\">作 {{other.author}}</p>\n                                        <div class=\"slider-footer\">\n                                            <star-rating v-model=\"other.rating\" :read-only=\"true\" :star-size=20 ></star-rating>\n                                        </div>\n                                        <div class=\"slider-user\">\n                                            <p class=\"test\">ユーザ名:{{other.user_name}}</p>\n                                        </div>\n                                    </div>\n                                </div>\n                            </router-link>\n                        </slide>\n                    </carousel>\n                    <div class=\"list-link\">\n                        <router-link v-bind:to=\"{name: 'book.other'}\">\n                            <p class=\"list-link_btn\">一覧表示する</p>\n                        </router-link>\n                    </div>\n                </div>\n            </div>\n            <div v-show=\"!resp\" class=\"my_data\">\n                <p class=\"heading\">自分の投稿(最近の投稿）</p>\n                <p class=\"not_data\" v-show=\"!checkMyData\">投稿がありません</p>\n                <div v-show=\"checkMyData\">\n                    <div v-for=\"myData in myDatas\" :key=\"myData.id\">\n                        <div class=\"my_data-content\">\n                            <div class=\"my_data-content_image\">\n                                <img v-bind:src=\"myData.imge\">\n                            </div>\n                            <div class=\"my_data-content_body\">\n                                <div class=\"my_data-content_body-title\">\n                                    <p class=\"tag\">タイ<br>トル</p>\n                                    <p class=\"content-main title\">{{myData.title}}</p>\n                                </div>\n                                <div class=\"my_data-content_body-author\">\n                                    <p class=\"tag\">著者</p>\n                                    <p class=\"content-main author\" >{{myData.author}}</p>\n                                </div>\n                                <div class=\"my_data-content_body-rating\">\n                                    <p class=\"tag\">評価</p>\n                                    <star-rating class=\"content-main rating\" v-model=\"myData.rating\" :read-only=\"true\" :star-size=30 ></star-rating>\n                                </div>\n                                <div class=\"my_data-content_body-footer\">\n                                    <p class=\"tag\">{{timeStamp(myData.created_at)}}</p>\n                                    <router-link class=\"btn\" :to=\"`/book/show/${myData.id}`\" >詳細へ</router-link>\n                                </div>\n                            </div>\n                        </div>\n                    </div>\n                    <div class=\"list-link\">\n                        <router-link v-bind:to=\"{name: 'book.mylist'}\">\n                            <p class=\"list-link_btn\">一覧表示する</p>\n                        </router-link>\n                    </div>\n                </div>\n                <div v-show=\"!checkMyData\" class=\"list-link\">\n                    <router-link v-bind:to=\"{name: 'book.create'}\">\n                        <p class=\"list-link_btn\">感想を書いてみましょう！</p>\n                    </router-link>\n                </div>\n            </div>\n            <div v-show=\"resp\" class=\"responsive\">\n                <p class=\"heading\">自分の投稿(最近の投稿）</p>\n                <p class=\"not_data\" v-show=\"!checkMyData\">投稿がありません</p>\n                <div v-show=\"checkMyData\">\n                    <div class=\"responsive_my-data\" v-for=\"myData in myDatas\" :key=\"myData.id\">\n                        <router-link class=\"responsive_my-data-show\" :to=\"`/book/show/${myData.id}`\">\n                            <div class=\"responsive_my-data_inner\">\n                                <div class=\"responsive_my-data_inner-header\">\n                                    <img v-bind:src=\"myData.imge\">\n                                </div>     \n                                <div class=\"responsive_my-data_body\"> \n                                    <p class=\"responsive_my-data_body-title\">{{myData.title}}</p> \n                                    <p class=\"responsive_my-data_body-author\">作 {{myData.author}}</p>\n                                    <div class=\"responsive_my-data_body-footer\">\n                                        <star-rating v-model=\"myData.rating\" :read-only=\"true\" :star-size=20 ></star-rating>\n                                    </div>\n                                </div>\n                            </div>f\n                        </router-link>\n                    </div>\n                    <div class=\"list-link\">\n                        <router-link v-bind:to=\"{name: 'book.mylist'}\">\n                            <p class=\"list-link_btn\">一覧表示する</p>\n                        </router-link>\n                    </div>\n                </div>    \n                <div v-show=\"!checkMyData\" class=\"list-link\">\n                    <router-link v-bind:to=\"{name: 'book.create'}\">\n                        <p class=\"list-link_btn\">感想を書いてみましょう！</p>\n                    </router-link>\n                </div>\n            </div>\n        </div>    \n    </div>\n</template>\n\n<script>\nexport default {\n    props:['user'],\n    data(){\n        return{\n            others:'',\n            checkOtherData: '',\n            checkMyData: '',\n            myDatas:'',\n            isLoading: true,\n            page: 3,\n            resp: false,\n            \n        } \n    },\n    methods:{\n\n    // みんなのデータ取得    \n        async getOtherData(){\n            const baseUrl = process.env.MIX_API_URL\n            await axios.get(`${baseUrl}/other/random`,{params:{user_id:this.user.id}})\n            .then((res)=>{\n                this.others = res.data.data\n                if(!this.others.length == 0){\n                    this.checkOtherData = true;\n                } else {\n                    this.checkOtherData = false;\n                }\n                console.log(this.others);\n                this.isLoading = false;\n            })\n            .catch((error) => {\n                console.log(error);\n                alert('みんなの投稿のデータ取得に失敗しました');\n            })\n        },\n\n    // 自分のデータ取得\n        async getMyData(){\n            const baseUrl = process.env.MIX_API_URL\n            await axios.get(`${baseUrl}/new_mydata`,{params:{user_id:this.user.id}})\n            .then((res) => {\n                this.myDatas = res.data.data;\n                if(!this.myDatas.length == 0) {\n                    this.checkMyData = true;\n                } else {\n                    this.checkMyData = false;\n                }\n                console.log(this.myDatas.length);\n                console.log(this.myDatas);\n                console.log(this.checkMyData);\n            })\n            .catch((error) => {\n                console.log(error);\n            })\n            console.log(this.baseUrl);\n        },\n\n    // 画面幅検知\n        handleResize(){\n            if(window.innerWidth <= 480){\n                this.page = 1;\n                this.resp = true;\n            }else if(window.innerWidth <= 705) {\n                this.page = 1;\n            }else if(window.innerWidth <= 1024) {\n                this.page = 2;\n                this.resp = true;\n            }else {\n                this.page = 3;\n                this.resp = false;\n            } \n        },\n\n    //topに戻るボタン\n        scrollTop(){\n            window.scrollTo({\n                top: 0,\n                behavior: 'smooth'\n            });\n        }\n    },\n\n    computed:{\n            timeStamp:function(){\n                return function(value) {\n                    return value.substring(0,10);\n                }\n            }\n        },\n    \n    created() {\n        window.addEventListener('resize',this.handleResize);\n        this.handleResize();\n        this.getOtherData();\n        this.getMyData();\n    }\n}\n</script>\n\n<style scoped>\n\n.body {\n    font-family: 'Avenir','Arial Rounded MT Bold','Comic Sans MS','Hannotate SC','Hiragino Kaku Gothic ProN','ヒラギノ角ゴ ProN W3','Yu Gothic UI',sans-serif;\n}\n\n/* topに戻るボタン */\n.top-btn {\n    color: #fff;\n    position: fixed;\n    right: 14px;\n    bottom: 35px;\n    width: 32px;\n    height: 32px;\n    line-height: 32px;\n    text-align: center;\n    border-radius: 50%;\n    background: #5bc8ac;\n}\n\n/* ローディングぐるぐる */\n.loading {\n    position:fixed;\n    top:0;\n    left:0;\n    width: 100%;\n    height: 100%;\n    background-color: rgba(255,255,255,1);\n    z-index: 80;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n\n}\n\n/* 投稿がありません */\n.not_data {\n    color: #999999;\n    font-size: 1.5rem;\n    text-align: center;\n    margin-top: 3rem;\n}\n\n/* 見出し */\n.heading {\n    display: inline-block;\n    font-weight: bold;\n    font-size: 1.5rem;\n    border-bottom: 2px solid #fff;\n}\n\n.heading:first-letter {\n    color: #FF5F17;\n    font-size: 3rem;\n}\n\n\n/* スライダー */\n.VueCarousel{\n    width: 90%;\n    margin: auto;\n}\n\n.VueCarousel-wrapper, .VueCarousel-inner, .VueCarousel-slide{\n    height: 100% !important;\n}\n\n.pagination\n.VueCarousel-dot {\n    background-color: red;\n}\n\n.VueCarousel-slide .slider-inner {\n    height: 180px;\n    width: 80%;\n    margin: 2rem auto 2rem;\n    padding: 0.2rem 0.5rem;\n    background-color: #d6edff; \n    display: flex; \n    color: #000; \n    box-shadow: 0px 0px 0px 10px #d6edff;\n    border: 2px dashed #fff;\n    font-size: 30px; \n    border-radius: 10px;\n}\n\n.slider-inner:hover {\n    transform: translateY(-3px);\n    box-shadow: 0 7px 14px rgba(50,50,93,1), 0 3px 6px rgba(0,0,0,.08);\n    transition: all .5s;\n}\n\n.slider-header {\n    width: 40%;\n    height: 100%;\n}\n\n.slider-body {\n    width: 50%;\n    margin-left: 0.6rem;\n}\n\n.slider-title {\n    font-size: 1rem;\n    font-weight: bold;\n    margin-left: 0.3rem;\n}\n\n.slider-author {\n    font-size: 0.8rem;\n    text-align: right;\n}\n\n.slider-header img {\n    width: 100%;\n    height: 100%;\n}\n\n\n\n.slider-footer {\n    display: flex;\n    font-size: 0.7rem; \n    margin-top: 0.6rem;\n}\n\n.slider-user {\n    margin-top: 0.6rem;\n    text-align: right;\n    font-size: 0.8rem;\n}\n\n\n/* 自分の投稿 */\n.my_data-content,.my_data-content_body-title, \n.my_data-content_body-author,.my_data-content_body-rating {\n    display: flex;\n    margin-top: 0.6rem;\n    border-bottom: 1px dashed #fff;\n}\n\n.my_data-content_body-footer {\n    display: flex;\n    margin-top: 0.6rem;\n    justify-content: space-between;\n}\n\n.my_data {\n    width: 100%;\n    margin-top: 5rem;\n}\n\n.my_data-content {\n    width: 900px;\n    height: 220px;\n    background: #FFDBC9;\n    padding: 0.2rem 0.5rem;\n    margin: auto;\n    margin-top: 5rem;\n    box-shadow: 0px 0px 0px 10px #FFDBC9;\n    border: 2px dashed #fff;\n}\n\n.my_data-content_body {\n    margin: 1rem 0 0 6rem;\n    width: 70%;\n}\n\n.my_data-content_image {\n    width: 15%;\n}\n\n.my_data-content_image img {\n    width: 100%;\n    height: 100%;\n}\n\n.tag {\n    font-size: 1rem;\n    width: 15%;\n}\n\n.content-main {\n    font-size: 2.3vmin;\n    margin-left: 5rem;\n}\n\n.title,.author, .rating {\n    margin-left: 7rem;\n}\n\n.btn {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    width: 30%;\n    height: 35px;\n    background-color: #FF5F17;\n    box-sizing: border-box;\n    color: #fff;\n    font-size: 16px;\n    letter-spacing: 0.1em;\n    line-height: 2.0;\n    text-decoration: none;\n    transition-duration: 0.3s;\n    position: relative;\n}\n\n.btn:before {\n    content: \"\";\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 0;\n    height: 0;\n    border-style: solid;\n    border-width: 15px 20px 0 0;\n    border-color: #ffffff transparent transparent transparent;\n}\n\n.btn:after {\n    content: \"\";\n    position: absolute;\n    top: -6px;\n    left: 5px;\n    width: 0;\n    height: 0;\n    border-style: solid;\n    border-width: 0 0 20px 15px;\n    border-color: transparent transparent #efefef transparent;\n    box-shadow: 1px 1px 1px 0px rgba(0, 0, 0, 0.15);\n    transform: rotate(16deg);\n}\n\n.btn:hover {\n    background-color: #e6de6b;\n}\n\n.btn span {\n    position: relative;\n    padding-left: 16px;\n    letter-spacing: 0.05em;\n}\n\n.btn span:before {\n    content: '';\n    width: 6px;\n    height: 6px;\n    border: 0;\n    border-top: solid 2px #fff;\n    border-right: solid 2px #fff;\n    transform: rotate(45deg);\n    position: absolute;\n    top: 50%;\n    left: 0;\n    margin-top: -4px;\n}\n\n\n/* 投稿を書いてみましょう,一覧表示 */\n.list-link {\n    display: flex;\n    justify-content: flex-end;\n}\n\n.list-link_btn {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    margin-top: 2rem;\n    margin-right: 3rem;\n    padding: 1rem 2rem;\n    width: 180px;\n    color: #fff;\n    font-size: 0.8rem;\n    background-color: #FF6600;\n    transition: 0.3s;\n}\n\n.list-link_btn::after {\n    content: '';\n    width: 7px;\n    height: 7px;\n    border-top: 3px solid #333333;\n    border-right: 3px solid #333333;\n    transform: rotate(45deg);\n}\n\n.list-link_btn:hover {\n    text-decoration: none;\n    background-color: #bbbbbb;\n}\n\n/* mydataレスポンシブ */\n.responsive {\n    margin-top: 5rem;\n}\n\n.responsive_my-data_inner {\n    height: 155px;\n    width: 75%;\n    margin: 5rem auto 2rem;\n    padding: 0.2rem 0.5rem;\n    background-color: #FFDBC9; \n    display: flex; \n    color: #000; \n    box-shadow: 0px 0px 0px 10px #FFDBC9;\n    border: 2px dashed #fff;\n    font-size: 30px; \n    border-radius: 10px;\n}\n\n.responsive_my-data_inner:hover {\n    transform: translateY(-3px);\n    box-shadow: 0 7px 14px rgba(50,50,93,1), 0 3px 6px rgba(0,0,0,.08);\n    transition: all .5s;\n}\n\n.responsive_my-data_inner-header {\n    width: 40%;\n    height: 100%;\n    /* padding: 0.2rem 0.5rem; */\n}\n\n.responsive_my-data_inner-header img {\n    width: 100%;\n    height: 100%;\n}\n\n.responsive_my-data_body {\n    width: 50%;\n    margin-left: 0.6rem;\n}\n\n.responsive_my-data_body-title {\n    font-size: 1rem;\n    font-weight: bold;\n    margin-left: 0.3rem;\n}\n\n.responsive_my-data_body-author {\n    font-size: 0.8rem;\n    text-align: right;\n}\n\n.responsive_my-data_body-footer {\n    display: flex;\n    font-size: 0.7rem; \n    margin-top: 0.6rem;\n}\n\n\n\n@media screen and (max-width: 1024px) {\n    body {\n        margin-top: 5rem;\n    }\n\n    .responsive_my-data_inner-header img {\n        width: 70%;\n    }\n\n    .responsive_my-data_body-title {\n        margin-top: 1rem;\n        font-size: 1.1rem\n    }\n\n    .responsive_my-data_body-author {\n        font-size: 0.9rem;\n    }\n\n    .responsive_my-data_body-footer {\n        font-size: 0.8rem; \n    }\n}\n\n@media screen and (max-width: 480px) {\n    body {\n        margin-top: 5rem;\n    }\n\n    .responsive_my-data_body-title {\n        margin-top: 0.3rem;\n        font-size: 1rem\n    }\n\n    .responsive_my-data_body-author {\n        font-size: 0.8rem;\n    }\n\n    .responsive_my-data_body-footer {\n        font-size: 0.7rem; \n    }\n}\n\n\n</style>"],"sourceRoot":""}]);
